/**
 *
 * Typography
 *
 */
@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/OpenSans-Regular-webfont.woff") format("woff"), url("../fonts/OpenSans-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/OpenSans-Semibold-webfont.woff") format("woff"), url("../fonts/OpenSans-Semibold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/OpenSans-Bold-webfont.woff") format("woff"), url("../fonts/OpenSans-Bold.ttf") format("truetype");
  font-weight: 800;
  font-style: normal;
}

html,
body {
  font-family: Helvetica, Arial, sans-serif;
  background-color: #fff;
}

h1 {
  color: #fff;
  font-weight: 200;
}

h2 {
  color: #222;
}

.node-content h2,
.article h2 {
  font-size: 1.5rem;
}

h3 {
  margin: 1.5em 0;
}

.node-content h3,
.article h3 {
  color: #81bd41;
  font-size: 1.25rem;
  font-weight: bold;
}

h4 {
  font-weight: bold;
}

.node-content h4,
.article h4 {
  font-size: 1.125rem;
  margin-top: 1em;
  color: #627e90;
  font-weight: normal;
}

code {
  font-size: 75%;
}

a {
  color: #005175;
}

a:hover {
  color: #005175;
}

.headline-base,
.sub-heading-base {
  color: #222;
  font-weight: normal;
  margin-bottom: 1.5em;
  margin-left: 10px;
}

.text-primary,
.highlight-text {
  color: #f58025;
}

.modal-body {
  background-image: linear-gradient(180deg, #EFEFEF 25%, #D1D3D4 64%, #D1D3D4 100%);
}

.modal-footer {
  background-color: #D1D3D4;
}

.modal-header .close {
  color: #006e9e;
  opacity: 1;
}

.modal-header .close:hover {
  color: #005275;
}

.node-content {
  background-color: #f5f9fa;
  padding: 0px;
}

/* ----------------------------------------------------------
//  Actions
// ---------------------------------------------------------- */
.cta-banner {
  background-color: #006e9e;
  color: #fff;
  max-height: 80px;
  display: flex;
  justify-content: center;
}

.cta-banner a {
  color: inherit;
}

.btn {
  border-radius: 3px;
}

.btn-primary {
  background-color: #509ccf;
  border-color: #509ccf;
  color: #fff;
  outline: 0;
}

.btn-primary:hover, .btn-primary:focus {
  background-color: transparent;
  border-color: #a0c9e5;
  color: #509ccf;
  outline: 0;
}

/*=========================================
=      Header CTA Banner & Navigation       =
=========================================*/
.agent {
  background: #509ccf;
  display: flex;
  justify-content: center;
  align-items: center;
}

.agent:after {
  border-left-color: #509ccf;
}

.header-cta-banner {
  display: flex;
  justify-content: center;
  background-color: #006e9e;
  color: white;
}

.header-cta-banner a {
  color: #fff;
}

.site-header .navbar-toggle {
  color: #222;
}

.site-header .navbar-toggle a {
  color: inherit;
}

@media only screen and (max-width: 62em) {
  .navbar .flex-nav a {
    font-weight: normal;
    text-decoration: none;
  }
  .navbar .flex-nav li a {
    border-top: 1px solid #ebebeb;
    text-decoration: none;
  }
  .navbar .flex-nav li a:focus, .navbar .flex-nav li a:active, .navbar .flex-nav li a:hover {
    background-color: #222;
    border-top-color: #222;
    color: #fff;
  }
}

.nav-primary a {
  color: #222;
}

.nav-primary a:hover {
  color: #222;
}

.nav-primary li:after {
  color: #222;
}

.order-status-nav a {
  color: #222;
}

.order-status-nav a:hover {
  color: #222;
}

.navbar-cart .badge {
  border-color: #81bd41;
  color: #81bd41;
}

/**
 *
 * Forms.
 *
 */
.form-control {
  border-radius: 0;
}

.form-control[placeholder] {
  font-style: italic;
}

.email-signup {
  border-color: rgba(255, 255, 255, 0.5);
  text-align: center;
}

