/* ----------------------------------------------------------
//  Hero section
// ---------------------------------------------------------- */
.node-page .hero,
.node-error-page .hero {
  background-color: #00436f;
}

.node-page .hero .headline-base,
.node-error-page .hero .headline-base {
  color: #fff;
}

