  border-color: #ebebeb;
}

@media only screen and (min-width: 47.875em) {
  .result .col:not(:first-child) {
    border-left-color: #ebebeb;
  }
}

.result__more {
  border-top: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;
}

@media only screen and (min-width: 47.875em) {
  .result__more {
    border: 0;
  }
}

.result__more-label {
  color: #006e9e;
  cursor: pointer;
  text-decoration: underline;
}

.result__more-label .glyphicon:before {
  margin-left: 0.5em;
}

.result__more-label:before {
  content: "More ";
}

.result__more-input {
  display: none;
}

.result__more-input:checked + label i:before {
  content: "\e113";
}

.result__more-input:checked + label:before {
  content: "Less ";
}

.result__btn-call {
  font-size: 14px;
}

@media only screen and (min-width: 47.875em) {
  .result__btn-select {
    padding-left: 0.5em;
    padding-right: 0.5em;
