}

@media only screen and (min-width: 47.875em) {
  #nameCol {
    text-align: left;
  }
}

#nameCol h3 {
  font-weight: bold;
  font-size: 14px;
  line-height: 20px;
}

@media only screen and (min-width: 47.875em) {
  #nameCol h3 {
    font-size: 13px;
    margin-top: 1.5em;
  }
}

#metaCol {
  text-align: center;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  color: #222;
  font-size: 24px;
  line-height: 2;
  vertical-align: text-bottom;
  padding-top: 0;
}

@media only screen and (max-width: 75em) and (min-width: 47.875em) {
  #metaCol {
    font-size: 20px;
  }
}

#metaCol sup {
  font-size: 100%;
  font-weight: normal;
  top: 0em;
}

