  color: #00436f;
  text-align: center;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}

#promoCol p {
  max-width: 100%;
}

#sortResultsListForm {
  height: 0 !important;
}

#callInNumber {
  color: #00436f;
  font-size: 14px;
}

#callInNumber strong {
  display: block;
}

.result__promo-short {
  color: #f58025;
}

