#results {
  background-color: #f5f9fa;
}

.result__meta {
  border-top: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;
}

.result__meta.highlight:before {
  color: #00436f;
}

.result__meta.col-bundles p:not(:last-child) {
  border-bottom-color: #ebebeb;
}

.result__provider-logo {
  border-left-color: #e3e4e4;
}

.disclaimer-link {
  font-size: 12px;
