.call-overlay .modal-header {
  background: url(../img/call-in-popup-agent-min.png) center 18% no-repeat;
}

.call-overlay .modal-body {
  background-image: none;
  background-color: #fff;
  border-color: #509ccf;
}

.call-overlay .modal-body .col {
  border-left: 0;
}

@media only screen and (max-width: 47.875em) {
  .call-overlay .modal-body .col {
    height: auto;
  }
}

.call-overlay .modal-footer {
  background-color: #509ccf;
  color: #fff;
}

.call-overlay__label {
  background: #fff;
  color: #5A5A5A;
}

.call-overlay__promo {
  color: #509ccf;
}

